import VueUtil from "@/util/VueUtil";
import UrlUtil from "@/util/UrlUtil";
import CollectionUtil from "@/util/CollectionUtil";
import aside_menu from "@/router/aside_menu";
import ObjectUtil from "@/util/ObjectUtil";
import Windows from "@/project/var/Windows";

export default {

    /**
     * 
     *  vue-router  route 
     * {
     *     path : '/index' ,
     *     query: {
                var1: yes
            }
     * }
     * @param route
     */
    toRoute(route) {
        VueUtil.getVue().$router.push(route)
    },

    /**
     *  path 
     * @param path  
     * @param query 
     */
    toPath(path, query = {}) {
        this.toRoute({
            path: path,
            query: query
        })
    },

    /**
     * 
     * 
     *  toRoute  toPath
     * @param to     path  
     * @param params    
     */
    to(to, params = {}) {
        if (typeof to === 'string') {
            this.toPath(to, params);
        } else {
            this.toRoute(to);
        }
    },

    /**
     *  hash 
     * @param params
     */
    setHash(params = {}) {
        let nowHash = UrlUtil.hash();
        nowHash = Object.assign(nowHash, params);
        location.hash = UrlUtil.toHashString(nowHash, false);
    },

    /**
     *  query 
     * 
     * @param params
     */
    setQuery(params = {}) {
        let nowQuery = UrlUtil.query();
        nowQuery = Object.assign(nowQuery, params);
        location.query = UrlUtil.toQueryString(nowQuery);
    },

    /**
     * 
     * @param query
     */
    toLogin(query = {}) {
        this.toRoute({
            path: '/Login',
            query: query
        })
    },
    /**
     * 
     * @param query
     */
    toBack(query = {}) {
        this.toRoute({
            path: '/Back',
            query: query
        })
    },

    toWindow(path, query = {}) {
        let route = CollectionUtil.findDeep(aside_menu, x => x.path === path);
        route.params = query;
        Windows.set(route);
    }
}