import StringUtil from "@/util/StringUtil";

export default {

    /**
     *  hash 
     *  #key=value 
     *  key  key 
     *  key  null
     * @param key    key
     * @returns {any}
     */
    hash(key = null) {
        let hash = window.location.hash;
        hash = this.splitKeyValue(hash.replace(/^#/, ''));
        return key === null ? hash : (hash[key] || null);
    },

    /**
     *  query 
     *  ?key=value 
     *  key  key 
     *  key  null
     * @param key    key
     * @returns {any}
     */
    query(key = null) {
        let query = window.location.search;
        query = this.splitKeyValue(query.replace(/^\?/, ''));
        return key === null ? query : (query[key] || null);
    },

    /**
     * 
     *  http://aaa.com/a/b/c.html  /a/b/c
     * @returns {string}
     */
    pathName() {
        return window.location.pathname.replace('.html', '');
    },

    /**
     *  k=v&k2=v2  
     *  '123', 'true'  
     * @param str   
     * @returns {{}}
     */
    splitKeyValue(str) {
        let ret = {};
        if (str === '') return {};
        let kvArr = str.split('&');
        kvArr.map(x => {
            let kv = x.split('=');
            let v = null;
            if (kv.length > 1) {
                v = StringUtil.tryConvert(kv[1]);
            }
            ret[kv[0]] = v;
        });
        return ret;
    },

    /**
     *  hash 
     *  #k=v&k2=v2
     * @param obj
     * @param prefix
     * @returns {string}
     */
    toHashString(obj, prefix = true ) {
        if (obj === null || Object.keys(obj).length === 0) {
            return '';
        }
        return ( prefix ? '#' : '') + this.objToKeyValue(obj);
    },

    /**
     *  query 
     * ?
     * @param obj
     * @returns {string}
     */
    toQueryString(obj) {
        if (obj === null || Object.keys(obj).length === 0) {
            return '';
        }
        return '?' + this.objToKeyValue(obj);
    },

    /**
     *  key=value 
     * @param obj
     * @returns {string}
     */
    objToKeyValue(obj) {
        let ret = [];
        for (let k in obj) {
            ret.push(`${k}=${obj[k]}`);
        }
        return ret.join('&');
    }

}
