/**
 * 
 */
import DialogUtil from "@/util/DialogUtil";
import UserHandle from "@/project/user/UserHandle";
import RouterUtil from "@/util/RouterUtil";

export default class ResponseCodeHandle {

    constructor() {
        this._json = {};
    }

    /**
     * json
     * @returns {{}|*}
     */
    getJson() {
        return this._json;
    }

    /**
     * 
     * @returns {boolean}
     * @param responseData
     */
    validate(responseData) {
        this._json = responseData;
        try {
            this._json = (responseData instanceof String) ? JSON.parse(responseData) : responseData;
            return this._identifyCode(this._json);
        } catch (e) {
            return false;
        }
    }

    /**
     *  code 
     * 
     * @private
     */
    _identifyCode() {
        switch (this._json.code) {
            case 200 :
                return this._json;
            case 100 :
                this._code100Handle();
                return false;
            case 110 :
                this._code110Handle();
                return false;
            case 120:
                this._code120Handle();
                return false;
            default :
                this._codeOtherHandle();
                return false;
        }
    }

    /**
     * code == 100
     * 
     * 
     * @private
     */
    _code100Handle() {
        DialogUtil.toastError(this._json);
    }

    /**
     * code == 110
     * 
     * 
     * @private
     */
    _code110Handle = () => {
        UserHandle.removeLoginUser();
        RouterUtil.toLogin();
        DialogUtil.toastError("");
    };

    /**
     * code == 120
     * 
     *
     * @private
     */
    _code120Handle = () => {
        DialogUtil.toastError("");
    };

    /**
     * 
     * @private
     */
    _codeOtherHandle = () => {
        DialogUtil.toastError('');
    };

}