export default {

    /**
     * 
     * 
     *  true  false
     * @param v
     * @returns {boolean}
     */
    isNotString(v) {
        let l = [
            /^\d+$/,
            /^(true)|(false)$/
        ];
        for (let reg of l) {
            if (reg.test(v)) {
                return true;
            }
        }
        return false;
    },

    /**
     * 
     *  'true'  true
     * '123123.20'  123123.2
     * @param v     
     * @returns {*}
     */
    tryConvert(v) {
        if (this.isNotString(v)) {
            try {
                v = eval(v);
            } catch (e) {
            }
        }
        return v;
    },
    /**
     * 
     * @param field
     * @returns {*}
     */
    toHump(field) {
        if (!field || field === '') return null;
        return field.replace(/\_(\w)/g, function (all, letter) {
            return letter.toUpperCase();
        });
    },

    /**
     * 
     * @param field
     * @returns {null}
     */
    toLine(field) {
        if (!field || field === '') return null;
        return field.replace(/([A-Z])/g, "_$1").toLowerCase();
    }

}
